<template>
    <div>
        <div class="h-96">
            <CardBox class="h-96">
                <div class="h-80"> 
                    <Line v-if="loaded" ref="myChart" id="myChart" :data="data" :options="options" />
                </div>
                <button @click="resetGraph">Reset</button>
            </CardBox>        
        </div>
    </div>
  </template>

<script>
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
} from 'chart.js'
import { Line } from 'vue-chartjs'
import zoomPlugin from 'chartjs-plugin-zoom';
import SectionTitle from "@/components/SectionTitle.vue";
import CardBox from "@/components/CardBox.vue";
import { mdiChartPie } from "@mdi/js";
import { options, data } from "@/views/chartConfig.js";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  zoomPlugin,
  Legend
)

export default {
components: {
    SectionTitle,
    CardBox,
    mdiChartPie,
    Line
},
props: {
    firstchart: {
      type: String,
      required: true
    },
    secondchart: {
      type: String,
      required: true
    }
  },
  data() {
    return {
        dashboard: { name: "Charts" },
        loaded: false,
        data: data,
        options: options,
    }
  },
  created() {
    this.getChartData();
  },
  methods: {
    async getChartData() {
        try {
            // First chart
            const response = await fetch(`https://api.llama.fi/charts/${this.firstchart}`);
            const data = await response.json();
            data.forEach((o) => o.date *= 1000); // Convert to milliseconds
            this.data.datasets[0].data = data;
            this.data.datasets[0].label = this.firstchart;
            console.log(data)
            // Second chart
            const responsetwo = await fetch(`https://api.llama.fi/charts/${this.secondchart}`);
            const datatwo = await responsetwo.json();
            datatwo.forEach((o) => o.date *= 1000); // Convert to milliseconds
            this.data.datasets[1].data = datatwo;
            this.data.datasets[1].label = this.secondchart;
            // Data loaded - now we can show the chart
            this.loaded = true
        } catch (error) {
            console.error(error);
        }
        },
    resetGraph() {
        this.$refs.myChart.chart.resetZoom()
    }
    }
}
</script>
